.about-bg {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 400px;
    text-align: center;
    background-attachment: fixed;
    color: #fff;
    position: relative;
    background-image: url('../images/mobile-app-developer.jpg');   
}

.about-us-container {
    padding: .6em 1em;
    @include container($width: 100%, $debug: false);
    @media only screen and (min-width: $lg) {
        padding: .6em 4em;

    }
    .about-heading {
        text-align: center;
        color: $accent-color;
        h1 {
            //font-size: 27px;
            span {
                font-weight: lighter;
            }
        }
        p {
            font-style: italic;
        }
        @media only screen and (min-width: $md) {
            h1 {
                font-size: 50px;
                span {
                    //font-weight: bold;
                }
            }
        }
    }
    .main-about {
        margin-bottom: 50px;
        margin-top: 50px;
        .single-section {
            @include span(12);
            .single-about {
                @media only screen and (min-width: $md) {
                    @include span(6);
                    margin-bottom: 20px;
                    margin-top: 20px;
                    &:nth-child(2n) {
                        @include span(last 6);
                        margin-right: 0px;
                    }
                }
                margin-bottom: 10px;
                margin-top: 10px;
                padding: 10px;
                h2 {
                    text-transform: uppercase;
                    //font-size: 20px;
                    font-weight: 300;
                    //line-height: 37px;
                    color: $accent-bright;
                    padding: 0px;
                    margin: 0;
                }
                margin-bottom: 0px;
                p {
                    //padding-top: 5px;
                    text-align: justify;
                    color: $secondary-text-color;
                }
                img {
                    width: 100%;
                    height: auto;
                }
            }
        }
    }
    /*--- Start Blog area Start here ---*/
    .blog-area {
        margin-bottom: 30px;
        margin-top: 30px;
        text-align: center;

        .single-blog {
            @media only screen and (min-width: $md) {
                @include span(6);
                &:nth-child(even) {
                    @include span(last 6);
                    margin-right: 0px;
                }
            }
            margin: 30px 0px;
            box-shadow: 0px 4px 6px 0px #d1d9d6;
            border: 1px solid#eef5f2;
            border-radius: 4px;
            padding: 20px;
            img {
                width: 100%;
                height: auto;
            }
            h2 {
                font-size: 28px;
                color: $accent-color;
                span {
                    font-weight: bold;
                    font-size: 33px;
                }
            }
            a {
                margin-top: 20px;
                padding: 8px 20px;
                border-radius: 30px;
                font-size: 16px;
            }
        }

    }
    /*--- We are spack ---*/
    .we-speak {
        text-align: center;
        margin-bottom: 30px;
        margin-top: 30px;
        .speak-heading {
            text-align: center;
            color: $accent-color;
            h2 {
                font-size: 23px;
                color: $accent-color;
                line-height: 35px;
                span {
                    font-weight: 600;
                    font-size: 30px;
                }
            }
        }
        .single-speak {
            @include span(2);
            margin: 2px;
            h2 {
                font-size: 23px;
                color: $accent-color;
                line-height: 35px;
                span {
                    font-weight: 600;
                    font-size: 30px;
                }
            }
        }

    }

    /*---- Some Of Our Hobbies ----*/
    .our-main-hobbies {
        text-align: center;
        margin-top: 30px;
        margin-bottom: 30px;
        h2 {
            font-size: 23px;
            color: $accent-color;
            line-height: 35px;
            span {
                font-weight: 600;
                font-size: 30px;
            }

        }

            .single-hobbies {
                text-align: center;
                li {
                    float: left;
                    padding: 4px 6px;
                    //line-height: 0px;
                    font-weight: 300;
                    list-style: none;
                    font-size: 14px;

                }
            }


    }
    .Newsleeter-section {
        text-align: center;
        margin-bottom: 30px;
        margin-top: 30px;
        border-top: 1px solid#ddd;
        h2 {
            font-size: 23px;
            color: $accent-color;
            line-height: 35px;
        }
        .input-custom {
            padding: 14px 10px !important;
            font-size: 14px !important;
            background: #ddd !important;
        }
        .btn-custom {
            @media only screen and (min-width: $sm) {
                padding: 12px 30px!important;
            }
            @media only screen and (min-width: $lg) {
                padding: 12px 30px!important;
                margin-top: -1px!important;
            }
            padding: 12px 30px;
            font-size: 16px;
            font-weight: 600;
            text-transform: uppercase;
            margin-left: -10px;
            border-radius: 0px 4px 4px 0px;
            margin-top: -3px;
            outline-style: none;
        }
    }

}



