/*========== Training ==================*/

.container-training {
    @include container($width: 100%, $debug: false);
    padding: 0.5em 1em;
    @media only screen and (min-width: $lg) {
        padding: 0.5em 4em;
    }
}

.training-details-section {
    @include container($width: 100%, $debug: false);
    padding: 0.5em 1em;
    @media only screen and (min-width: $lg) {
        padding: 0.5em 4em;
    }

}

.banner-training {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 400px;
    text-align: center;
    background-attachment: fixed;
    color: $color-gray-50-lightest;
    position: relative;
    background-image: url('../images/training.jpg');
    .page-title {
        //letter-spacing: 1px;
        font-size: 1.4rem;
        font-weight: bold;
        //color: #FFFFFF;
        //line-height: 1.2em;
    }
    .page-slogan {
        font-size: 1.7rem;
        color: $primary-color-text;
        //line-height: 40px;
        font-weight: lighter;
        padding-bottom: 18px;
    }
    a{
        font-size: 1.2rem;
    }
    a:hover {
        box-shadow: 1px 1px 4px 0px #eef5f2;
        transition: all 0.1s ease-in-out 0s;
    }

    .image-overlay {
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.7);
        position: absolute;
        left: 0;
        padding: 138px 0px;
    }
    @media only screen and (min-width: $md) {
        .image-overlay {
            padding: 158px 0px;
        }
        .page-title {
            //font-size: 42px;
            //line-height: 0.6em;
        }
    }
    @media only screen and (min-width: $lg) {
        .image-overlay {
            padding: 130px 0px;
        }
        .page-title {
            font-size: 2em;
            //line-height: 0.6em;
        }
        .page-slogan {
            //line-height: 1em;
            //font-size: 1.7rem;
            //padding-bottom: 22px;
        }
    }
}

/*=========== Trainer body =============*/
.bn{ font-family: 'SolaimanLipi', sans-serif; }
.tr-heading {
    color: $accent-color;
    text-align: center;
    h1 {
        //font-size: 20px;
        margin-top: 1rem;
        line-height: 1.5em;
        font-weight: lighter;
        font-size: 1.6em;
    }
    span {
        font-size: 30px;
        font-weight: 600;
        letter-spacing: 1px;
        color: $accent-color;
        line-height: 1.6em;
    }
}

.tr-content {
    @media only screen and (min-width: $lg) {
        //margin-top: 30px;
    }
    @include span(12);

    .heading {
        @media only screen and (min-width: $lg) {
            @include span(4);
            h2 {
                margin: 0px;
                height: 455px !important;
                line-height: 42px !important;
                padding: 34px !important;
                font-size: 20px!important;
                border-radius: 0px!important;
            }
        }
        h2 {
            height: auto;
            background: #eef5f2;
            box-shadow: 0px 6px 10px 0px rgba(209, 207, 207, 0.64);
            line-height: 36px;
            width: 100%;
            font-size: 17px;
            padding: 15px;
            border: 8px solid $primary-color-light;
            border-radius: 4px;

        }
    }
    .main-example {
        @media only screen and (min-width: $lg) {
            @include span(last 8 narrow);

        }
    }
    .example {
        position: relative;
        border-radius: 5px;

        .md-ali {
            @media only screen and (min-width: $md) {
                @include span(6 narrow);

            }
            img {
                @media only screen and (min-width: $md) {
                    width: 106%;
                    height: 352px;
                    border-radius: 0px;
                }
                height: auto;
                width: 100%;
                border-radius: 5px 5px 0px 0px;
            }
        }
        h4 {
            font-size: 17px;
            line-height: 30px;
            padding: 12px 14px;
        }
        .example-text {
            position: relative;
            width: 100%;
            background: #eef5f2;
            @media only screen and (min-width: $md) {
                @include span(last 6 narrow);
                height: 352px;
                margin: 0px;

                line-height: 51px;
                padding: 56px;

            }
            p {
                font-size: .9rem;
                font-style: italic;
                line-height: 1.8;
                /* padding: 20px 13px;*/
            }
            i {
                font-size: medium;
                color: $accent-color;
                padding: 0px 3px;
                //display: block;
            }
            i:last-child {
                text-align: right;
            }
        }
    }
}

.example-text:after,
.example-text:before {
    @media only screen and (min-width: $md) {
        display: block;
    }
    right: 100%;
    top: 30%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    display: none;
}

.example-text:after {
    border-color: rgba(136, 183, 213, 0);
    border-width: 30px;
    margin-top: -30px;
}

.example-text:before {
    border-color: rgba(194, 225, 245, 0);
    border-right-color: #eef5f2;
    border-width: 35px;
    margin-top: -35px;
}

.sussget-text {
    width: 100%;
    @media only screen and (min-width: $md) {
        @include span(all);
    }

    background: #eef5f2;
    padding: 5px 14px;
    margin-top: 15px;
    border-radius: 4px;

    h4 {
        line-height: 30px;
        font-size: 15px;
        font-weight: 400;
    }
}

/*==========  CODE FOR ABOUT AREA ==========*/

.what-are-you-get {
    //padding-top: 30px;
    h2 {
        text-align: center;
        line-height: 1.6em;
        //font-size: 18px;
        font-weight: 500;
        color: $accent-color;
        border-bottom: 1px solid #ddd;
        padding-bottom: 20px;
    }
    .bollet {
        li {
            list-style: disc;
            line-height: 30px;
            margin: 0px 14px;
            p {
                padding-left: 52px;
                margin-bottom: 1.5rem;
                margin-top: 0px;
            }
        }
    }
}

.messanger-text {
    //padding-bottom: 30px;
    p {
        //padding-top: 21px;
        text-align: justify;
        font-size: .9rem;
    }
}

/*============  Massengers area section ============*/

.messangers {
    //padding-bottom: 30px;
    margin-top: 30px;
}

.custom {
    @media only screen and (min-width: $md) {
        @include span(6);
        &:nth-child(2n) {
            @include span(last 6);
            margin-right: 0px;
        }
    }
    text-align: center;
    width: 100%;
    background: #eef5f2;
    box-shadow: 0 4px 10px 0px rgba(210,
    208,
    208,
    0.64);
    border-radius: 5px;
    padding: 22px 0px;
    margin-bottom: 20px;
    p {
        padding: 0px 13px;
        text-align: center;
        font-size: .8rem;
    }
    h4 {
        color: $color-gray-700-dark;
        line-height: 1px;
    }
}

.custom img {
    width: 33%;
    border-radius: 100%;
    height: 33%;
}

/*T========= Trining area start here =========*/

.training-section {
    @include container($width: 100%, $debug: false);
    @media only screen and (min-width: $lg) {
        padding-bottom: 60px !important;
        padding: 0.5em 4em;

    }
    padding: 0.5em 1em;
    margin-top:30px;
    background: #eef2f5;
    .border-bottom {
        border-bottom: 1px solid #ddd;
        padding-bottom: 10px;
        font-weight: 400;
        font-size: 22px;
        color: $accent-color;
    }
    .single-course {
        @media screen and (min-width: $md) {
            @include span(6);
            &:nth-child(2n + 1) {
                @include span(last 6);
                margin-right: 0px;
            }
        }
        @media screen and (min-width: $lg) {
            @include span(4);
            &:nth-child(2n + 1) {
                @include span (4);
            }
            &:nth-child(3n + 1) {
                @include span(last 4);
                margin-right: 0px;
            }
        }
        background: #fff;
        border-radius: 5px;
        box-shadow: 0 4px 10px 0px rgba(210,
        208,
        208,
        0.64);
        padding: 20px 13px;
        //font-size: 13px;
        margin: 20px 0px;
        h2 {
            font-weight: initial;
            font-size: 1.6rem;
            line-height: 1.8rem;
            margin-top: auto;
            margin-bottom: auto;
            padding-left: 0px;
            padding-right: 0px;
        }
        img {
            width: 100%;
            height: auto;
            padding: 0px;
        }
        p {
            //padding-bottom: 15px;
            -webkit-hyphens: auto;
            -moz-hyphens: auto;
            -ms-hyphens: auto;
            hyphens: auto;
            text-align: justify;
            font-size: .8em;
        }
        .custom-enroll {
            border-top: 1px solid$accent-color;
            padding-top: 10px;
            font-size: 1rem;

            a {

                span{
                    color: $accent-bright;
                }
                &:nth-child(even) {
                    float: right;
                    font-size: small;
                    line-height: 1.4rem;
                    font-style: italic;
                }
            }
            a:hover span{
                border-radius: 7px 7px 0px 7px;
                -moz-border-radius: 7px 7px 0px 7px;
                -webkit-border-radius: 7px 7px 0px 7px;
                border: 2px solid $accent-bright-2;
                color: $accent-bright-2;
                padding: 4px;
            }

        }
    }
}

.title-image{
    text-align: center;
    img{
        border: 1px solid #dedfe0;
        box-shadow: 0 0 1px 1px rgba(20,23,28,.1), 0 3px 1px 0 rgba(20,23,28,.1);
        max-width: 100%;
    }
}
.row{
    .col6{
        text-align: center;
        list-style-type: none;
        @include span(6);
        &:nth-child(even){
            @include span(last 6);
            margin-right: 0px;
        }
    }
}
.info-box {
    background-color: $color-gray-100-lighter;
    border: 1px solid $color-gray-500;
    padding: 10px 15px;
    box-shadow: 0 0 1px 1px rgba(20,23,28,.1), 0 3px 1px 0 rgba(20,23,28,.1);
    border-radius: 4px;
    font-size: .8rem;
    width: 80%;
    margin-left: 10%;
}
