/*=========== contact-us ===========*/

.contact-us {

    @include container($width: 100%, $debug: false);
    padding: 0.6em 1em;
    @media only screen and (min-width: $lg) {
        padding-bottom: 30px !important;
        padding: 0.6em 4em;


    }
    .text-center {
        text-align: center;
        font-size: 20px;
        color: $accent-color;
        line-height: 37px;
    }
    h3 {
        font-size: 1.2rem;
        font-weight: 400;
    }
    p {
        font-size: 14px;
        line-height: .6rem;
    }
    .single-content {
        @media only screen and (min-width: $md) {
            @include span(4);
            &:nth-child(2n + 1) {
                @include span(last 4);
                margin-right: 0px;
                text-align: right;
            }
        }
        @media only screen and (min-width: $lg) {
            @include span(4);
        }
    }
}

.single-content.last-column {
    @media screen and (min-width: $md) {
        text-align: center;
    }
    padding-bottom: 17px;
    margin-top: 24px;
    h3 {
        line-height: 2px;
        font-weight: 400;
    }

    a {
        color: $accent-color;
        font-size: 2rem;
    }
}

.trainig-con-button {
    margin-bottom: 30px;
    margin-top: 30px;
    text-align: center;
    background: #fff;
    padding-bottom: 20px;
    border: 1px solid#eef5f2;
    border-radius: 4px;
    box-shadow: 0px 4px 6px 0px #d1d9d6;
    border-left: 5px solid$accent-color;
    a {
        @media screen and (min-width: $md) {
            padding: 16px 30px;
            font-size: 16px;
            margin-top: 25px;
            text-transform: uppercase;
            font-weight: 600;
             box-shadow: 0px 4px 8px 0px #eef5f2;
        }
        padding: 18px;
        font-size: 15px;
        margin-top: 25px;
       

    }
    a:hover {
        transition: all 0.5s ease-in-out 0s;
        background: #106c63;

    }
}

